  methods: {
    next () {
      if (this.current === 1) {
        this.current = 0;
      } else {
        this.current += 1;
      }
    },
    setGyl(option){
      const elem = this.xqList.find(item => item.campusid == option)
      const index = elem.value
      const list = this.gylData.filter(item => item.parentId == index)
      this.gylList = list
    },
    setNj(option){
      const list = this.njData.filter(item => item.collegekey == option)
      this.njList = list
    },
    setBj(option){
      const list = this.bjData.filter(item => item.gradekey == option)
      this.bjList = list
    },
    genFile(){
      let dataList = ["import json\n"+
      "import random\n"+
      "import re\n"+
      "import time\n"+
      "\n"+
      "import requests\n"+
      "requests.packages.urllib3.disable_warnings()\n"+
      "from bs4 import BeautifulSoup\n"+
      "\n"+
      "\n"+
      "def getPid():\n"+
      "    S = requests.Session()\n"+
      "    S.headers.update({\n"+
      "        'User-Agent': 'Mozilla/5.0 (Linux; Android 10; CLT-AL00 Build/HUAWEICLT-AL00; wv) AppleWebKit/537.36 (KHTML, like Gecko) Version/4.0 Chrome/77.0.3865.120 MQQBrowser/6.2 TBS/045332 Mobile Safari/537.36 V1_AND_SQ_8.4.8_1492_YYB_D QQ/8.4.8.4810 NetType/4G WebP/0.3.0 Pixel/1080 StatusBarHeight/81 SimpleUISwitch/0 QQTheme/1000 InMagicWin/0',\n"+
      "        'Content-Type': 'application/x-www-form-urlencoded;charset=utf-8'\n"+
      "    })\n"+
      "    S.verify = False\n"+
      "    try:\n"+
      "        r = S.get('https://ehall.jlu.edu.cn/jlu_portal/login')\n" +
      "        soup = BeautifulSoup(r.text, 'lxml')\n" +
      "        pid = soup.find('input', attrs={'name':'pid'}).get('value')\n"+
      "        return {'S': S, 'pid': pid}\n"+
      "    except Exception as e:\n"+
      "        print(str(e))\n"+
      "        return False\n"+
      "\n"+
      "\n"+
      "def login(name, pwd, pid, S):\n"+
      "    params = {\n"+
      "        'username': name,\n"+
      "        'password': pwd,\n"+
      "        'pid': pid\n"+
      "    }\n"+
      "    try:\n"+
      "        response = S.post(\n"+
      "            'https://ehall.jlu.edu.cn/sso/login',\n"+
      "            data=params)\n"+
      "        soup = BeautifulSoup(response.text, 'lxml')\n"+
      "        if soup.title.text == '吉林大学网上办事大厅':\n"+
      "            return S\n"+
      "        else:\n"+
      "            return False\n"+
      "    except requests.exceptions.ConnectionError as e:\n"+
      "        print('连接错误，重试')\n"+
      "        print(e)\n"+
      "        return 'ConnectError'\n"+
      "    except Exception as e:\n"+
      "        print('出现登录错误')\n"+
      "        print(str(e))\n"+
      "        return False\n"+
      "\n"+
      "def getFormPage(url, S):\n"+
      "    response = S.post(\n"+
      "        url,\n"+
      "        verify=False\n"+
      "    )\n"+
      "    soup = BeautifulSoup(response.text, 'lxml')\n"+
      "    result = {\n"+
      "        'url': response.url,\n"+
      "        'csrf': soup.find('meta', itemscope='csrfToken')['content']\n"+
      "    }\n"+
      "    return result\n"+
      "\n"+
      "def getFormInfo(number, url, crsf, S):\n"+
      "    pargam = {\n"+
      "        'stepId': number,\n"+
      "        'admin': 'false',\n"+
      "        'csrfToken': crsf\n"+
      "    }\n"+
      "    S.headers.update({'referer': url})\n"+
      "    response = S.post(\n"+
      "        'https://ehall.jlu.edu.cn/infoplus/interface/render',\n"+
      "        data=pargam\n"+
      "    )\n"+
      "    return response.json()['entities'][0]['data']\n"+
      "\n"+
      "def makeFormData(url, formdata):\n"+
      "    formdata['_VAR_URL'] = url\n"+
      "    if formdata['fieldXY1'] == '1':\n"+
      "        formdata['fieldZhongtw'] = '1'\n"+
      "    if formdata['fieldXY2'] == '1':\n"+
      "        formdata['fieldWantw'] = '1'\n"+
      "    formdata['fieldDJXXyc'] = '1'\n"+
      "    return formdata\n"+
      "\n"+
      "def postmsg(number, csrf, forminfo, S):\n"+
      "    times = int(time.time())\n"+
      "    data = json.dumps(forminfo)\n"+
      "    boundFields = ','.join(forminfo.keys())\n"+
      "    pargam = {\n"+
      "        'formData': data,\n"+
      "        'nextUsers': '{}',\n"+
      "        'rand': random.uniform(100, 999),\n"+
      "        'boundFields': boundFields,\n"+
      "        'timestamp': times,\n"+
      "        'csrfToken': csrf,\n"+
      "        'stepId': number,\n"+
      "        'actionId': '1'\n"+
      "    }\n"+
      "    try:\n"+
      "        response = S.post(\n"+
      "            'https://ehall.jlu.edu.cn/infoplus/interface/doAction',\n"+
      "            data=pargam,\n"+
      "            verify=False)\n"+
      "        return response.json()['errno'] == 0\n"+
      "    except:\n"+
      "        return False\n"+
      "\n"+
      "def postMsg(r):\n"+
      "    Sresult = getPid()\n"+
      "    S = login(r[0], r[1], Sresult['pid'], Sresult['S'])\n"+
      "    if S == 'ConnectError':\n"+
      "        postMsg(r)\n"+
      "    elif S:  # 登录\n"+
      "        url = 'https://ehall.jlu.edu.cn/infoplus/form/BKSMRDK/start'  # 获取url\n"+
      "        resurl = getFormPage(url, S)  # 获取csrf和返回的url\n"+
      "        times = time.strftime('%Y-%m-%d %H:%M:%S', time.localtime(time.time()))\n"+
      "        forminfo = getFormInfo(resurl['url'].split('/')[-2], resurl['url'], resurl['csrf'], S)\n"+
      "        forminfo = makeFormData(resurl['url'], forminfo)\n"+
      "        postmsg(resurl['url'].split('/')[-2], resurl['csrf'], forminfo, S)\n"+
      "        print('打卡成功,时间:' + times)\n"+
      "\n"+
      "if __name__ == '__main__':\n"+
      "    r = ["+
      '"' + this.formInline.user + '",' +
      '"' + this.formInline.password + '",' +
      '"' + this.formInline.xq + '",' +
      '"' + this.formInline.gyl + '",' +
      '"' + this.formInline.nj + '",' +
      '"' + this.formInline.bj + '",' +
      '"' + this.formInline.qsh + '"'
      +"]\n"+
      "    postMsg(r)\n"
      ]

      if (this.formInline.xq && this.formInline.bj && this.formInline.nj && this.formInline.gyl && this.formInline.qsh && this.formInline.password && this.formInline.user){
        const FileSaver = require('file-saver');
        const blob = new Blob(dataList, {type: "text/plain;charset=utf-8"});
        FileSaver.saveAs(blob, "user.py");
        this.next();
      }
    }
  }
